@import url('https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@20..48,100..700,0..1,-50..200');

body {
   margin: 0;
   font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
      'Droid Sans', 'Helvetica Neue', sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
}

/* Handle on hover */

.header {
   display: flex;
   align-items: center;
   width: 100%;
   max-height: 100px;
   height: 100px;
   box-sizing: border-box;
}

.wrapper {
   display: flex;
   height: calc(100vh - 100px);
}

.shopList {
   display: flex;
   flex-direction: column;
   flex-basis: 30%;
   flex-grow: 0;

   ul {
      padding-left: 0;
   }

   &__heading,
   &__listElement {
      margin: 14px auto;
   }

   &__listElement {
      text-align: center;
      display: block;
      font-size: 16px;
      line-height: 18px;
      list-style-type: none;
      width: auto;
      max-width: 60%;
      border: 1px solid gray;
      border-radius: 16px;
      padding: 18px 40px;
      cursor: pointer;
      &:hover {
         background-color: #c1c1c1;
         color: Orangered;
      }
   }
}

.shopMenu {
   flex-basis: 60%;
   flex-grow: 1;
   display: flex;
   flex-wrap: wrap;
   height: 100%;
   overflow-y: scroll;

   &__item {
      display: flex;
      flex-direction: column;

      width: 300px;
      height: 270px;
      border-radius: 20px;
      margin: 10px 0 10px 20px;
      padding: 12px 8px;
      position: relative;

      &:nth-child(n) {
         background: rgba(122, 179, 243, 0.2);
      }
      &:nth-child(2n),
      &:nth-child(7n) {
         background: rgba(233, 121, 178, 0.2);
      }
      &:nth-child(3n) {
         background: rgba(215, 215, 249, 0.2);
      }
      &:nth-child(4n) {
         background: rgba(120, 247, 187, 0.2);
      }

      &_frame {
         display: flex;
         flex-direction: column;
         align-items: flex-start;
         padding: 0px;
         width: auto;
         height: 136px;
         left: 168.92px;
         top: 42.63px;
         margin: 12px 18px;

         h4 {
            width: 100%;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden;
         }
      }

      &_name {
         width: 158px;
         height: 36px;
         font-family: Poppins;
         font-style: normal;
         font-weight: normal;
         font-size: 18px;
         line-height: 18px;
         color: #000000;
         padding-right: 10px;
         margin: 0;
      }

      &_price {
         width: 158px;
         font-weight: bold;
         font-size: 24px;
         line-height: 32px;
         color: #000;
         margin: 0 0 14px 0;
         &::before {
            content: '$';
         }
      }

      &_image {
         display: block;
         content: '';
         margin-top: -18px;
         margin-left: -12px;
         width: 148px;
         height: 145px;
         filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
         border-radius: 50%;
         box-sizing: border-box;
      }

      &_button {
         display: flex;
         flex-direction: row;
         padding: 6px 20px;
         font-family: Poppins;
         font-style: normal;
         font-weight: bold;
         font-size: 16px;
         line-height: 24px;
         text-align: center;
         color: #ffffff;
         background-color: #6b00f5;
         border-radius: 20px;
         border: #d7d6fc;
         cursor: pointer;
         margin-bottom: 0;
         align-self: flex-end;
         justify-self: flex-end;
         &:hover {
            background-color: #a5a5a571;
            color: #6b00f5;
         }

         &.delete {
            color: #ffffff;
            background-color: rgb(116, 41, 41);

            span {
               position: absolute;
               display: block;
               font-size: 40px;
               font-weight: bold;
               right: 10px;
               top: 5px;
               color: yellowgreen;
               font-variation-settings: 'FILL' 0, 'wght' 400, 'GRAD' 0, 'opsz' 48;
            }

            &:hover {
               background-color: #00000071;
               color: #000;
            }
         }
      }
   }
}

.cart_heading {
   position: absolute;
   top: 0;
   right: 50px;
}

.cart_wrapper {
   ::-webkit-scrollbar {
      width: 4px;
   }

   /* Track */
   ::-webkit-scrollbar-track {
      // background: #f1f1f1;
      background: #ffffff;
   }

   /* Handle */
   ::-webkit-scrollbar-thumb {
      background: orange;
      border-radius: 5px;
   }

   &__cartSection {
      &_inputCount {
         &::-webkit-inner-spin-button {
            cursor: pointer;
            padding: 2px 10px;
            margin: -4px 4px;
            opacity: 1;
         }
      }
   }

